body {
  background-color: #0f224a;
  color: #F8F8F8;
}

main {
  padding: 1rem;
  margin: auto;
  min-width: 300px;
  max-width: 70rem;
}

.flex, .flex-row, .flex-col {
  display: flex;
}
.flex-col {
  flex-direction: column;
}
.flex-row {
  flex-direction: row;
}
.flex-grow-1 {
  flex: 1;
}
.flex-grow-2 {
  flex: 2;
}
.flex-grow-3 {
  flex: 3;
}
.flex-grow-4 {
  flex: 4;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.grid {
  display: grid;
  gap: 1rem;
}

.w-100 {
  width: 100%;
}
.w-75 {
  width: 75%;
}
.w-50 {
  width: 50%;
}
.w-25 {
  width: 25%;
}

.section-main {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  gap: 1rem;
}
.section-emails-list {
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 90%;
  max-width: 40rem;
}

.button {
  position: relative;
  display: block;
  padding: 1rem;
  border: 0;
  border-radius: 1rem;
  background-color: #ceaa24;
  color: #111111;
  text-align: center;
  cursor: pointer;
}
.button:hover {
  background-color: #eecd53;
}
.button:disabled {
  background-color: #796312;
  cursor: auto;
}
.button [class^=icon-], .button [class*=" icon-"] {
  position: absolute;
  right: 1rem;
}

.button-group {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.button-group .button {
  width: 200px;
}

.card {
  width: 100%;
  padding: 1rem;
  border: 2px solid #ceaa24;
  border-radius: 1rem;
  background-color: #205AD5;
  font-family: "Slabo 13px", serif;
}
.card h1, .card h2 {
  text-align: center;
  font-family: "Milonga", serif;
}

.data-list-container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  max-height: 40rem;
  width: 100%;
  max-width: 40rem;
}
.data-list-container .list-search {
  border-radius: 0;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.data-list-container .list-search .input-text:focus {
  border-top-right-radius: 1rem;
}
.data-list-container .data-list {
  margin: 0;
  padding: 0;
  overflow: auto;
  background-color: #020b1d;
  min-height: 20rem;
  border-top: 2px solid #ceaa24;
  border-bottom: 2px solid #ceaa24;
}
.data-list-container .list-item {
  display: block;
  list-style: none;
  padding: 1rem;
  background-color: #0f337b;
  border-bottom: 2px solid #111111;
}
.data-list-container .list-item:hover {
  background-color: #5178c6;
}
.data-list-container .list-item.selected {
  background-color: #385fac;
}
.data-list-container .add-item-form .button {
  border-radius: 0;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  width: 100%;
}

.input-group {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: #0f337b;
}
.input-group:focus-within {
  background-color: #0f224a;
}
.input-group:focus-within + .error-message {
  background-color: #0f224a;
}
.input-group .input-text {
  background-color: transparent;
  width: 100%;
  color: #F8F8F8;
  border: 0;
  padding: 1rem;
}
.input-group .input-text:focus {
  border: 0;
  outline: 0;
  background-color: #0f224a;
}
.input-group .input-label {
  padding: 1rem;
}

.error-message {
  display: none;
  padding: 0.5rem;
  font-size: 0.9rem;
  background-color: #0f337b;
  color: #F24E1E;
  text-align: right;
}
.error-message#gallery-error {
  display: block;
  background-color: transparent;
}

.section-gallery {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex: 3;
}

.section-gallery-generator {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 1rem;
}

.gallery {
  border: 2px solid #ceaa24;
  border-radius: 1rem;
  padding: 0 1rem;
  width: auto;
  background-color: #4c7ce4;
}
.gallery .gallery-container {
  overflow: auto;
  justify-content: center;
  display: grid;
  grid-template-columns: repeat(1, 200px);
  gap: 1rem;
  padding: 1rem 0;
  height: 30rem;
}
@media (min-width: 768px) {
  .gallery .gallery-container {
    grid-template-columns: repeat(2, 200px);
  }
}
@media (min-width: 1024px) {
  .gallery .gallery-container {
    grid-template-columns: repeat(3, 200px);
  }
}

.gallery-generator {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  align-items: flex-end;
}
.gallery-generator .button-group {
  max-width: 200px;
}
.gallery-generator .button-group .error-message {
  text-align: center;
}

.image {
  border: 2px solid #111111;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
  background-color: #F8F8F8;
  font-family: "icomoon";
  color: #848484;
  font-size: 5rem;
  text-align: center;
}
.image::after {
  content: "\e90d";
}
.image.loading::after {
  content: "\e97f";
  animation: spin 2s linear infinite;
  z-index: 9;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}